
.header .btn {
  color: black !important;
  height: 100%;
}

.header .btn-link {
  font-size: 1.5em !important;
}
  

.header .btn:hover {
  color: #ff6b6b !important;
}

/* Video Section Styling */
.video-section {
  margin-bottom: 20px;
}

.video-container {
  background-color: black;
}

.video-info h5 {
  margin-bottom: 10px;
}

.video-info p {
  margin: 0;
}

.video-creator img {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 8px;
  overflow: hidden !important;
  display: inline-block;
}
.video-stats{
  margin-left: 10px !important;
}

.video-stats button {
  /* color: white; */
  border: none;
  font-size: 1.2em !important;
}

.video-stats .text-succes {
  color: green !important;
}

.video-stats .text-danger {
  color: #dc3545;
}

/* Comments Section Styling */
.comments-section {
  background-color: #fcfcfc;
  padding: 10px;
  border-radius: 5px;
}

.message-text {
  margin-left: 145px !important;
}

.comment-actions {
  align-items: end;
}

.comment-actions .text-succes {
  color: green !important;
}


.comment-actions .btn{
  font-size: 1.2em !important;
}


.add-comment {
  display: flex;
  align-items: flex-start;
  margin-top: 2px;
}

.add-comment textarea {
  flex: 1;
  padding: 5px;
  border-radius: 15px;
  height: 10px;
}

.add-comment button {
  margin-top: 2px;
  padding: 3px;
  margin-left: 10px;
  border: none;
  border-radius: 15px ;
  background-color: #007bff;
  color: white;
}

.comments-list .comment {
  display: flex;
  align-items: flex-start;
  margin-top: 10px;
  gap: 10px;
}

.comment img {
  width: 36px;
  height: 36px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 8px;
  overflow: hidden !important;
  display: inline-block;
}

.comment-body {
  /* background-color: #000; */
  padding: 10px;
  border-radius: 5px;
  width: 100%;
}

.comment-body-header {
  display: flex;
  justify-content: space-between;
}

.comment-body-header strong {
  margin-right: 10px;
}

.comment-body p {
  margin: 0;
}

.editing-comment {
  width: 100%;
  padding: 5px;
  border-radius: 5px;
  resize: vertical;
}

.comment-body .comment-actions {
  display: flex;
  gap: 10px;
  margin-top: 0px;
}

.comment-body .comment-actions button {
  /* background: black; */
  border: none;
  /* color: white; */
  cursor: pointer;
}

.comment-body .comment-actions button:hover {
  /* color: white; */
}

/* Recommendations Section Styling */
.recommendations-section {
  /* background-color: #0f0f0f; */
  /* color: black; */
  background-color: #fcfcfc;
  margin-left: 120px !important;
  border-radius: 20px;
}

.recommended-videos {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.recommended-video {
  display: flex;
  align-items: flex-start;
  gap: 10px;
}

.recommended-video img {
  width: 168px;
  height: 94px;
  border-radius: 5px;
}

.recommended-video-info {
  /* color: #aaa; */
  color: black;
}


.recommended-video-info h6 {
  margin: 0;
  font-size: 14px;
}

.recommended-video-info p {
  margin: 0;
  font-size: 12px;
}

.btn-link{
  font-size: medium !important;
/* color: white;  */
}

.btn-link:hover{
color: #ff6b6b;
}

.btn-user-login-videoPage{
  margin-top: 6px !important;
  width: 36px;
  height: 36px;
}

.bi-share{
  color: blue !important;
}