.channel-banner {
    display: flex;
    align-items: center;
    height: 200px;
    width: 100%;
    background-color:lavender;

}

.user-page {
    font-family: 'Roboto', sans-serif;
}


.scrolling-text {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 50%;
}


.user-info {
    display: flex;
    align-items: center;
    /* padding: 10px; */
}


.btn-edit-user {
    padding: 0px;
    margin-top: 150px;
    background-color: none !important;
    font-size: 1.3rem !important;
    border: none;
    margin-right: 5px;
}




.profile-pic {
    width: 160px;
    height: 160px;
    border-radius: 50%;
    margin-left: 10px;
}

.user-details {
    flex-grow: 1;
}

.subscribe-btn {
    background-color: #cc0000;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 2px;
}

.channel-nav {
    display: flex;  
    border-bottom: 1px solid #303030;
    padding: 10px 0;
}

.channel-nav a {
    color: black;
    text-decoration: none;
    padding: 0 20px;
}

.channel-nav a.active {
    color: black;
    border-bottom: 3px solid #fff;
}

.video-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 50px;
    padding: 20px;
}

.video-card {
    border-radius: 10px;
    overflow: hidden;
}

.video-card img {
    width: 100%;
    height: 200px;
    border-radius: 10px;
}

.video-info {
    padding: 10px;
}

.video-info h3 {
    margin: 0;
    font-size: 14px;
    font-weight: bold;
    size: 1rem;

}

.video-info p, .video-info span {
    font-size: 12px;
    margin: 5px 0;
}